body {
  font-family: Arial, Roboto, "PingFang SC", "Droid Sans","Hiragino Sans GB";
  background: #F8F9FA;
  color: #8e8e93;
  font-size: 1.4rem;
  overflow-x: hidden;
}
/*loading公共加载样式 start*/
.loading-wrap {
  text-align: center;
  padding: 1rem 0;
}
.loading-wrap i, .loading-wrap span {
    vertical-align: middle;
}
.loading-wrap i {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url(data:image/png;base64,) 0 0/100% 100% no-repeat;
    animation: roll 1s linear infinite;
    -webkit-animation: roll 1s linear infinite;
}
.loading-wrap span {
    font-size: 1.5rem;
    color: #82888f;
}
@keyframes roll {
from {
    transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
}
to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
}
}
@-webkit-keyframes roll {
from {
    transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
}
to {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
}
}
/*loading公共加载样式 end*/
/*加载失败公共加载样式 start*/
.loaded, .load-error {
  font-size: 1.3rem;
  padding: 1rem 0;
  text-align: center;
  color: #8e8e93;
}
/*加载失败公共加载样式 end*/
.hide {
  display: none !important;
}
.task-container .task-item {
  padding: 1.35rem 1rem;
  background: #fff;
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
      -ms-flex-align: start;
          align-items: flex-start;
  position: relative;
}
.task-container .task-item:after {
    content: '';
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid #E0E0E0;
    -webkit-transform: scale(0.5, 0.5);
    transform: scale(0.5, 0.5);
    transform-origin: top left;
    -webkit-transform-origin: top left;
    z-index: 1;
}
.task-container .task-item:last-child:after {
    border-bottom: 0;
}
